.category {
  $self: &;

  &__container {
    position: relative;
    padding-bottom: 4.3rem;
    max-width: 150rem;

    @include mobile {
      padding-bottom: 0;
    }
  }

  &__slider {
    @include mobile {
      margin-bottom: 2.4rem;
    }
  }

  &__slide {
    @include mobile {
      display: none;

      &:first-child {
        display: block!important;
      }
    }
  }

  &__list {
    margin: -1.5rem -1.3rem;
    display: flex;
    flex-wrap: wrap;
    position: relative;

    @include mobile {
      margin: -.6rem;
    }
  }

  &__li {
    padding: 1.5rem 1.3rem;
    width: calc(100% / 3);

    @include mobile {
      width: 100%;
      padding: .6rem;
    }

    &.hide {
      display: none;
    }

    @include fromDesktop {
      width: calc(100% / 4);
    }

    @include smallDesktop {
      width: calc(100% / 3);
    }

    @include lessHorizontalTablet {
      width: calc(100% / 2);
    }

    @include lessVerticalTablet {
      width: 100%;
    }
  }

  .tns {
    &-nav {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: -.6rem;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;

      button {
        width: 1rem;
        height: 1rem;
        border: none;
        border-radius: 50%;
        background: #cecece;
        transition: background .25s;
        margin: .6rem;
        display: inline-block!important;

        &.tns-nav-active {
          background: red;
        }
      }
    }
  }

  &__show-more {
    margin: 0 auto;
    margin-top: 3rem;
    width: 20rem;

    @include mobile {
      display: block;
    }
  }
}
