.category-slider {
  background: #e5e5e5;
  position: relative;
  height: 3.5rem;
  overflow: hidden;
  $self: &;

  &__container {
    height: auto;

    @include mobile {
      padding: 0!important;
    }

    &:before, &:after {
      display: block;
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      background: no-repeat center;
      background-size: 100% auto;
      width: 2.3rem;
      z-index: 20;
    }

    &:before {
      left: 2rem;
      background-image: url('../img/css/category-slider-grad-left.png');

      @include mobile {
        left: 0;
      }
    }

    &:after {
      right: 2rem;
      background-image: url('../img/css/category-slider-grad-right.png');

      @include mobile {
        right: 0;
      }
    }
  }

  &__list {
    height: 3.5rem;
  }

  &__li {
    height: 100%;
  }

  &__item {
    display: flex;
    align-items: center;
    height: 100%;

    #{$self}__li:first-child & {
      margin-left: 2rem;
    }

    #{$self}__li:last-child & {
      margin-right: 2rem;
    }
  }

  &__link {
    color: #000;
    font-size: 1.3rem;
    transition: color .3s;

    @include hover {
      color: $red;
    }
  }

  // tns
  .tns-ovh {
  }

  .tns-outer {
    position: relative;
    vertical-align: bottom;
  }

  .tns-horizontal.tns-subpixel > .tns-item {
    vertical-align: bottom;
  }

  .tns-controls {
    height: 0;

    button {
      position: absolute;
      top: calc(50% - .4rem);
      transform: translateY(-50%);
      font-size: 0;
      width: .7rem;
      height: 1.2rem;
      background: no-repeat center;
      background-size: 100% auto;
      z-index: 50;

      &:first-child {
        left: -.5rem;
        background-image: url(../svg_icons/arrow-slider-left.svg);
      }

      &:last-child {
        right: -.5rem;
        background-image: url(../svg_icons/arrow-slider-right.svg);
      }
    }
  }
}
