.cart-popup {
  position: absolute;
  top: calc(100% + 1.5rem);
  right: 0;
  width: 51.6rem;
  background: #fff;
  color: #000;
  z-index: 50;
  box-shadow: 0 5px 24px rgba(#000, .20);
  padding: 1.5rem 0 2rem;
  border: 1px solid rgba(#000, .12);
  opacity: 0;
  pointer-events: none;
  transition: opacity .3s;
  cursor: auto;
  $contentPadding: 0 1rem 0 4.5rem;
  &.active {
    opacity: 1;
    pointer-events: auto;
  }

  &:after {
    display: block;
    content: '';
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 8.5px 9px 8.5px;
    border-color: transparent transparent #fff transparent;
    position: absolute;
    right: 10rem;
    bottom: calc(100%);
  }
  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 1.5rem;
    top: -15px;
  }

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    border-bottom: 1px solid $grey;
    margin-bottom: 1.8rem;
    padding: $contentPadding;
    padding-bottom: 1.1rem;
  }

  &__header-text {
    font-size: 1.7rem;
    font-weight: 500;
  }

  &__header-btn {
    margin-left: auto;
    width: 17.5rem;
    height: 4.4rem;
    font-size: 1.5rem;
  }

  &__list {
    margin: -2rem;
  }

  &__li {
    padding: 2rem;
    position: relative;

    &:after {
      display: block;
      content: '';
      position: absolute;
      left: 2rem;
      right: 2rem;
      top: 100%;
      height: 1px;
      background: $grey;
      transform: translateY(-50%);
    }
    &:hover {
      background: #e6e6e6;
    }
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    position: relative;
  }

  &__content-container {
    padding: $contentPadding;
  }

  &__overflow-container {
    overflow-y: auto;
    overflow-x: hidden;
    margin: 0 -1rem 0 -4.5rem;
    padding: $contentPadding;
    max-height: 45rem;
  }

  &__card-header {
    width: 100%;
    font-size: 1.4rem;
    padding-bottom: .8rem;
    border-bottom: 1px solid $grey;
    margin-bottom: 1.3rem;
  }

  &__text-bold {
    font-weight: bold;
  }

  &__img-container {
    position: relative;
    width: 11rem;
    max-height: 11rem;
  }

  &__img-box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    transform: translateX(-.4rem);
  }

  &__img {
    max-height: 100%;
  }

  &__content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 1.4rem;
    padding-right: 2.8rem;
  }

  &__title {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: .7rem;
  }

  &__title-text {
    font-size: 1.3rem;
    font-weight: 400;
    line-height: 1.7rem;
    color: #000;
    transition: color .3s;

    @include hover {
      color: $red;
    }
  }

  &__close {
    position: absolute;
    font-size: 1rem;
    background: #dcdcdc;
    color: #fff;
    line-height: 0;
    border-radius: 100%;
    width: 22px;
    height: 22px;
    left: -3.4rem;
    top: calc(50% + .9rem);
    transform: translateY(-50%);
    transition: background .3s;

    &:hover {
      background: #E02020;
    }
  }

  &__desc {
    font-size: 1.1rem;
    line-height: .9rem;
    color: #9299a2;
  }

  &__price {
    font-size: 1.5rem;
    line-height: 3rem;
    color: #4a4a4a;
    margin-left: 1.9rem;
  }
  &__item-total {
    color: #000;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 3rem;
    display: flex;
    width: 100%;
    margin-left: 13rem;
    justify-content: space-between;
  }

  &__total {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    font-size: 1.8rem;
    font-weight: 500;
    line-height: 3rem;
    color: #E02020;
    padding: $contentPadding;
    padding-top: .9rem;
    padding-bottom: 1rem;
    border-top: 1px solid $grey;

    &:after {
      display: block;
      content: '';
      position: absolute;
      left: 4.5rem;
      right: 1rem;
      top: 100%;
      height: 1px;
      background: $grey;
      -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
    }
  }

  &__btn-checkout {
    width: 17.5rem;
    height: 4.4rem;
    margin-top: 2rem;
  }

  &__counter {
    padding-top: .3rem;
  }
  .btn__text {
    margin-top: 3px;
  }
}
