.social {
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: -.5rem;

    @include mobile {
      margin: -.75rem;
    }
  }

  &__li {
    padding: .5rem;

    @include mobile {
      padding: .75rem;
    }
  }

  &__img {
    width: 4.8rem;
    height: 4.8rem;

    @include mobile {
      width: 2.8rem;
      height: 2.8rem;
    }
  }
}
