.cart {
  $self: &;

  &__content {
    max-width: 130rem;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;

    @include mobile {
      display: block;
    }
  }

  &__left-col {
    flex: 1;
    padding-right: 4rem;

    @include desktop {
      padding-right: 1rem;
    }

    @include mobile {
      flex: none;
      padding-right: 0;
    }
  }

  &__right-col {
    width: 31.3rem;
    padding-top: 3.6rem;

    @include mobile {
      width: 100%;
      padding-top: 0;
    }
  }

  &__header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 1.5rem;

    @include mobile {
      width: calc(100% + 3rem);
      margin-left: -1.5rem;
      border-bottom: 1px solid #ccc;
      padding: 0 1.5rem;
      padding-bottom: .7rem;
      margin-bottom: .2rem;
    }
  }

  &__quantity, &__amount {
    font-size: 1.6rem;
    font-weight: bold;

    @include mobile {
      font-size: 1.2rem;
    }
  }

  &__quantity-key, &__amount-key {
    color: #7F7F7F;
  }

  &__amount-key {
    margin-right: .4rem;
  }

  &__total {
    padding: 3rem 4.4rem;
    background: #e5e5e5;
    display: flex;
    flex-wrap: wrap;

    @include desktop {
      padding: 3rem 2.2rem;
    }

    @include mobile {
      display: block;
      width: calc(100% + 3rem);
      margin-left: -1.5rem;
      margin-top: 1.8rem;
    }
  }

  &__total-header {
    font-size: 2.7rem;
    line-height: 3.4rem;
    font-weight: bold;
    width: 15.3rem;

    @include mobile {
      font-size: 2rem;
      line-height: normal;
      width: 100%;
      margin-bottom: 1.2rem;
    }
  }

  &__total-content {
    margin-left: 18rem;
    padding-top: .6rem;

    @include desktop {
      margin-left: auto;
    }
  }

  &__total-list {
    margin: -1.0rem;
  }

  &__total-li {
    position: relative;
    padding: 1.0rem;

    &:after {
      display: block;
      content: '';
      position: absolute;
      left: 0;
      top: 100%;
      right: 0;
      height: 1px;
      background: #D3D3D3;
    }
  }

  &__total-item {
    font-size: 1.7rem;
    color: #868686;
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
    text-align: right;

    #{$self}__total-li:last-child & {
      color: $black;
    }
  }

  &__total-key {
    font-weight: 500;
    flex: 1;
  }

  &__total-value {
    margin-left: 6rem;
    min-width: 7.5rem;
  }

  &__info-list {

  }

  &__info-li {
    &:last-child {
      .card-info {
        border-top: none;
      }
    }
  }

  &__btn-order {
    margin-top: 3rem;
  }
}
