.suppliers {
  $self: &;
  color: #000;

  &__header {
    font-size: 1.6rem;
    font-weight: bold;
    margin-bottom: 1.8rem;

    @include mobile {
      font-size: 1.4rem;
      margin-bottom: 1.4rem;
    }
  }

  &__table-container {
    @include mobile {
      overflow: auto;
      max-width: calc(100vw - 3rem);
      white-space: nowrap;
    }
  }

  &__table {
    width: 100%;
    border-collapse: collapse;

    @include mobile {
      min-width: 50rem;
    }
  }

  &__td {
    font-size: 1.3rem;
    border-bottom: 1px solid #C7C7C7;
    padding-top: 1.2rem;
    padding-bottom: 1.2rem;

    @include mobile {
      font-size: 1.1rem;
    }

    &:first-child {
      width: 10rem;

      @include desktop {
        width: auto;
      }
    }

    &:last-child {
      @include desktop {
        display: none;
      }
    }

    &--head {
      color: #7F7F7F;
      padding-top: 0;
      border-bottom: none;
      font-size: 1.2rem;
      padding-bottom: 0.3rem;
    }
  }
}
