.services {
  position: relative;

  @include mobile {
    max-width: 100vw;
    width: calc(100% + 3rem);
    padding-bottom: 2.4rem;
    padding-left: 1.5rem;
    margin-bottom: 2.3rem;
    margin-left: -1.5rem;
  }

  .tns-ovh {
    @include fromDesktop {
      height: auto!important;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -1rem;

    @include mobile {
      margin: 0;
      display: block;
    }
  }

  &__li {
    padding: 1rem;
    width: calc(100% / 3);

    @include mobile {
      width: 100%;
      padding: 0;
    }
  }

  &__card {
    display: block;
    height: 20rem;
    background: rgba(#000, .25);
    transition: background .3s;
    padding: 2.2rem 1rem 2rem 2rem;

    @include mobile {
      height: auto;
      padding: 2.2rem 1.9rem 2.3rem;
    }

    @include hover {
      background: $red;
    }
  }

  &__header {
    @include roboto;
    font-size: 2rem;
    font-weight: bold;
    line-height: 2.3rem;
    color: #000;
    margin-bottom: 1.4rem;

    @include mobile {
      font-size: 1.6rem;
      margin-bottom: 1rem;
    }
  }

  &__desc-list {
    margin: -.2rem;
  }

  &__desc-li {
    padding: .2rem;
  }

  &__desc {
    font-size: 1.5rem;
    color: #fff;
    letter-spacing: 0.05rem;

    @include mobile {
      font-size: 1.2rem;
      line-height: 2.2rem;
      letter-spacing: 0.04rem;
    }
  }
}
