.tns {
  &-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: -6px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 20;

    button {
      width: 10px;
      height: 10px;
      border: none;
      border-radius: 50%;
      background: #cecece;
      transition: background .25s;
      margin: 6px;
      display: inline-block!important;

      &.tns-nav-active {
        background: red;
      }
    }
  }
}
