.mobile-menu {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #000;
  z-index: 999;
  padding-top: 1.2rem;
  overflow: auto;
  overflow-x: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity .3s;
  max-height: 100vh;
  max-width: 100vw;

  &.active {
    opacity: 1;
    pointer-events: auto;
  }

  &__container {
    padding: 0 1.3rem;
  }

  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 3rem;
  }

  &__close {

  }

  &__welcome {
    margin-left: auto!important;
  }

  .page-header {
    &__location {
      @include mobile {
        margin: 0 0 0 auto;
        display: flex;
      }
    }

    &__profile {
      @include mobile {
        display: flex;
        padding: 0;
        margin-right: -.2rem;
        margin-left: 2.8rem;
      }
    }

    &__profile-icon {
      @include mobile {
        margin-right: 0;
      }
    }

    &__profile-text {
      @include mobile {
        display: none;
      }
    }
  }

  &__profile {
    margin-left: 2rem;
  }

  &__content {
    padding: 0 1.3rem;
  }

  &__select-list {
    margin: -.5rem;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 5rem;
  }

  &__select-li {
    padding: .5rem;
  }

  &__list {
    margin: -.8rem;
  }

  &__li {
    padding: .8rem;
  }

  &__title {
    @include roboto;
    font-size: 1.6rem;
    font-weight: bold;
    color: #fff;
    margin-bottom: .2rem;
  }

  &__links {
    margin: -.3rem;
  }

  &__links-li {
    padding: .3rem;
  }

  &__link {
    @include circe;
    font-size: 1.4rem;
    color: #b6b6b6;
  }
}
