.price-box {
  border: 4px solid $black;
  padding: 2.3rem;
  font-size: 0;
  margin-bottom: 4.2rem;

  @include mobile {
    margin-bottom: 2rem;
    padding: 1.1rem 2.3rem 2.3rem;
  }

  &__price {
    font-size: 3.7rem;
    color: $red;
    margin-bottom: 1.1rem;

    @include mobile {
      font-size: 3rem;
      margin-bottom: .4rem;
    }
  }

  &__price-text {
    @include mobile {
      font-weight: normal;
    }
  }

  &__price-number {
    margin-left: .6rem;
    font-weight: bold;
  }

  &__quantity {
    display: inline-flex;
    font-size: 1.3rem;
    color: #fff;
    padding: .2rem 1.2rem;
    background: #BFBFBF;
    border-radius: 1.5rem;
    margin-bottom: 3.3rem;

    @include mobile {
      margin-bottom: 2.3rem;
    }
  }

  &__counter {
    margin-bottom: 1.5rem;

    @include mobile {
      margin-bottom: 1.3rem;
    }
  }

  &__cart {
    margin-bottom: .9rem;

    @include mobile {
      margin-bottom: 1.1rem;
    }

    .btn {
      &__icon {
        width: 2.7rem;
      }
    }
  }
}
