.page-header {
  $self: &;
  background: #000;
  color: #fff;
  height: 5.8rem;
  margin-bottom: 3.5rem;
  position: relative;
  z-index: 999;

  @include mobile {
    height: 4.6rem;
    margin-bottom: 1.2rem;
    position: sticky;
    top: 0;
  }

  &--static {
    height: auto;
    margin-bottom: 1.8rem;

    .navigation__logo {
      position: absolute;
      top: 1.5rem;
      left: 2rem;
    }

    .page-header__welcome {
      margin-left: 25rem;

      @include mobile {
        margin-left: 1.2rem;
      }
    }

    .navigation__search {
      margin-left: 0;
    }

    @include mobile {
      height: auto;
      position: static;
      padding-bottom: 0;

      &.active {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        z-index: 999;
        height: 4.6rem;

        .navigation, .category-carousel {
          opacity: 0;
          pointer-events: none;
        }
      }
    }

    .category-select {
      border-width: 0 1px 0 0;
    }
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    height: 5.8rem;

    @include mobile {
      height: 4.6rem;
    }

    #{$self}--static & {
      height: 5.8rem;

      @include mobile {
        height: 4.6rem;
      }
    }
  }

  &__welcome {
    font-size: 1.4rem;
    font-weight: bold;
    padding-left: 3rem;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      width: 20px;
      height: 20px;
      left: 0;
      background: url(../svg_icons/turkey-flag.svg) no-repeat center;
      border-radius: 100%;
    }

    @include mobile {
      margin-left: 1.2rem;
    }

    &--desktop {
      @include mobile {
        display: none;
      }
    }
  }

  &__location {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-right: 12.9rem;
    padding-left: .2rem;
  }

  &__location-icon {
    margin-right: .8rem;
  }

  &__location-text {
    font-size: 1.4rem;
    font-weight: bold;
    letter-spacing: 0.04rem;
  }

  &__location-red {
    color: $red;
  }

  &__select-list {
    display: flex;
    flex-wrap: wrap;

    @include mobile {
      display: none;
    }
  }

  &__select-li {
    padding: .5rem;
  }

  &__layers {
    margin-left: auto;
    color: #fff;
    line-height: 0;
    display: none;
    @include mobile {
      display: block;
    }
  }

  &__ring {
    position: relative;
    margin-left: auto;
    // @include mobile {
    //   margin-left: 1.9rem;
    // }
  }

  &__ring-counter {
    width: 1.6rem;
    height: 1.6rem;
    background: $red;
    border-radius: 50%;
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(1.4rem, -1.1rem);
    font-size: 1.1rem;
    font-weight: bold;
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;

    @include mobile {
      display: none;
    }
  }


  &__layers-counter {
    width: 1.6rem;
    height: 1.6rem;
    background: $red;
    border-radius: 50%;
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(1.4rem, -1.1rem);
    font-size: 1.1rem;
    font-weight: bold;
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;

    @include mobile {
      display: none;
    }
  }

  &__profile {
    margin-left: 3.6rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-right: 0.5rem;
    position: relative;

    @include mobile {
      display: none;
    }
  }

  &__profile-dropdown {
    display: none;
    position: absolute;
    top: calc(100% - 1px);
    right: 0;
    z-index: 50;
    box-shadow: 0 0 10px 1px rgba(#000, .20);

    #{$self}__profile:hover & {
      display: block;
    }
  }

  &__profile-wrapper {
    background: #fff;
    padding: 1.8rem 2rem 1.8rem 4rem;
    margin-top: .8rem;
    position: relative;

    &:before {
      display: block;
      content: '';
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 9px 8px 9px;
      border-color: transparent transparent #ffffff transparent;
      position: absolute;
      right: 2.1rem;
      bottom: calc(100%);
    }
  }

  &__profile-list {
    margin: -.6rem;
  }

  &__profile-li {
    padding: .6rem;
  }

  &__profile-link {
    display: block;
    font-size: 1.3rem;
    color: #000;
    font-weight: bold;
    text-align: right;
    transition: color .3s;

    @include hover {
      color: $red;
    }
  }

  &__profile-icon {
    margin-right: 1.7rem;
  }

  &__profile-text {
    font-size: 1.4rem;
    font-weight: bold;
    letter-spacing: 0.03rem;
  }

  &__profile-red {
    color: $red;
  }

  &__cart {
    display: none;

    @include mobile {
      display: block;
      margin-left: 1.9rem;
      margin-top: .3rem;
    }
  }

  &__cart-icon {
    font-size: 1.9rem;
    color: #fff;
  }

  &__menu {
    display: none;

    @include mobile {
      display: block;
      position: relative;
      width: 1.9rem;
      height: 1.6rem;
      margin-left: -.2rem;
      margin-top: -.2rem;
    }
  }

  &__menu-icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: opacity .3s;

    &--open {

    }

    &--close {
      opacity: 0;
    }
  }

  &__logo {
    display: none;

    @include mobile {
      display: block;
      margin-left: 1.5rem;
      margin-top: .2rem;
    }
  }

  &__logo-desktop {
    font-size: 1.4rem;
    font-weight: bold;

    @include mobile {
      display: none;
    }
  }

  &__static-container {
    display: flex;
    flex-wrap: wrap;
  }

  &__static-col-left {
    width: 23rem;
    padding-top: 4.2rem;
    margin-bottom: -.4rem;

    @include mobile {
      display: none;
    }
  }

  &__static-col-right {
    width: calc(100% - 23rem);
    padding-left: 2rem;
    margin-top: -0.4rem;

    @include mobile {
      width: 100%;
      padding-left: 0;
      margin-top: 0;
    }
  }

  &__about-title {
    font-size: 1.3rem;
    color: rgba(#fff, .5);
    margin-bottom: -.3rem;
  }

  &__about-header {
    font-size: 1.6rem;
    font-weight: bold;
    color: #9299A2;
  }
}
