.category-card {
  position: relative;
  padding: 3rem 1rem 3rem 3.2rem;
  border: 4px solid #000;
  height: 100%;

  @include mobile {
    padding: 2rem 1rem 6rem 2.1rem;
  }

  @include lessVerticalTablet {
    height: auto;
  }

  &__header {
    @include roboto;
    font-size: 2.5rem;
    color: #000;
    font-weight: 700;
    position: relative;
    z-index: 5;
    display: block;
    margin-bottom: .8rem;

    @include mobile {
      font-size: 1.8rem;
      margin-bottom: 1.3rem;
    }
  }

  &__header-red {
    color: #E02020;
  }

  &__list {
    position: relative;
    z-index: 5;
  }

  &__link {
    font-size: 1.4rem;
    line-height: 2.8rem;
    color: #4b4b4b;
    transition: color .3s;

    @include mobile {
      font-size: 1.2rem;
      line-height: 2.1rem;
    }

    @include hover {
      color: #000;
    }
  }

  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 55%;

    @include mobile {
      max-width: 15.6rem;
    }
  }
}
