.circle-counter {
  min-width: 1.6rem;
  height: 1.6rem;
  line-height: 1.6rem;
  padding: 0 .5rem 0 0.4rem;
  background: $red;
  border-radius: 20px;
  position: absolute;
  right: 0;
  top: 0;
  transform: translate(1.4rem, -1.1rem);
  font-size: 1.1rem;
  line-height: normal;
  font-weight: bold;
  color: #fff;
  z-index: 5;
  text-align: center;
  line-height: 1.6rem;
}
