.page-footer {
   background: #000;
   padding: 4.7rem 0 4.2rem;
   color: #fff;
   margin-top: 9.4rem;
   overflow-x: hidden;
   overflow-y: visible;

   &.hide {
     opacity: 0;
   }

   @include mobile {
     margin-top: 4rem;
     padding: 2.6rem 0 2.1rem;
   }

  &__contact-col {
    @include mobile {
      margin-bottom: 1.9rem;
    }
  }

  &__head {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid rgba(#d8d8d8, .3);
    padding-bottom: 3rem;
    padding-left: .8rem;
    margin-bottom: 2.2rem;

    @include mobile {
      display: block;
      padding-bottom: 0;
      padding-left: 0;
      border-bottom: none;
      margin-bottom: 2.3rem;
    }
  }

   &__header {
     @include roboto;
     font-size: 2.2rem;
     line-height: 2.3rem;
     font-weight: bold;
     margin-bottom: 1.3rem;

     @include mobile {
       font-size: 1.7rem;
       line-height: 2.3rem;
       margin-bottom: .8rem;
     }

     &--contact {
       margin-bottom: 3rem;

       @include mobile {
         margin-bottom: .7rem;
       }
     }
   }

   &__phone {
     display: flex;
     flex-wrap: wrap;
     align-items: center;
   }

   &__phone-icon {
     margin-right: 2rem;

     @include mobile {
       width: 3.8rem;
       height: 3.8rem;
       margin-right: 1.7rem;
     }
   }

   &__phone-text {
      @include roboto;
      font-size: 3.1rem;
      font-weight: bold;
      color: #fff;

      @include mobile {
        font-size: 2.2rem;
      }
   }

   &__email {
     @include circe;
     font-size: 1.4rem;
     color: #fff;
     padding-left: 7.6rem;

     @include mobile {
       display: none;
     }
   }

   &__links-col {
     margin-left: 6rem;

     @include mobile {
       margin-left: 0;
     }
   }

   &__category {
      margin: -3rem;
      display: flex;
      flex-wrap: wrap;

      @include mobile {
        margin-bottom: 0;
        li:nth-child(1) {
          width: 100%;
        }
      }
      @include mobile {
        display: none;
      }
   }

   &__category-li {
     padding: 3rem;

     &--customer-service {
       padding-left: 3.4rem;
       padding-right: 2.3rem;
     }
   }

   &__links {

   }

   &__links-li {

   }

   &__link {
     @include circe;
     font-size: 1.4rem;
     line-height: 3rem;
     color: #b6b6b6;
     transition: color .3s;

     @include hover {
       color: #fff;
     }
   }

  &__social-col {
    margin-left: auto;

    @include mobile {
      display: none;
    }

    &--mobile {
      display: none;

      @include mobile {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-left: 0;
      }
    }
  }

  &__social {
    margin-right: 4rem;
  }

  &__bot {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

   &__copy-date {
     @include circe;
     font-size: 1.4rem;
     margin-bottom: .5rem;

     @include mobile {
       font-size: 1.2rem;
       margin-bottom: .8rem;
     }
   }

   &__copy-desc {
     @include circe;
     font-size: 1.4rem;
     color: rgba(#fff, .6);

     @include mobile {
       font-size: 1.2rem;
       max-width: 21.1rem;
     }
   }

   &__by {
     margin-top: .5rem;

     @include mobile {
       display: none;
     }

     &--mobile {
       display: none;

       @include mobile {
         display: block;
       }
     @include mobile {
       width: 8.2rem;
     }
     }
   }

   &__by-img {
     @include mobile {
       width: 8.2rem;
     }
   }
}
