.category-carousel {
  position: relative;
  margin-left: -1.5rem;
  width: calc(100% + 3rem);
  padding-left: 1.5rem;

  &:after {
    display: block;
    content: '';
    width: 8.9rem;
    height: 5.9rem;
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 20;
    background: url(../img/css/category-carousel-bg.png) no-repeat center;
    background-size: 100% auto;

    @include mobile {
      display: none;
    }
  }

  &__li {
    opacity: 0;

    &.tns-item {
      opacity: 1;
    }

    &:last-child {
      .category-carousel__item-box {
        display: flex;
        &:after {
          display: inline-block;
          content: '';
          width: 8.7rem;
          height: 50px;
        }
      }
    }
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: auto;
    height: 100%;
    padding: 0 1rem;
    height: 3.1rem;
    border: 1px solid rgba(#fff, .5);
    transition: border-color .3s, background .3s;

    @include mobile {
      height: 3.6rem;
    }

    @include hover {
      border-color: $red;
      background: $red;
    }
  }

  &__icon {
    margin-right: .9rem;
    max-width: 16px;
    position: relative;
    top: -1px;

    @include mobile {
      width: 2rem;
      height: 2rem;
      margin-right: 1.2rem;
    }
  }

  &__text {
    @include roboto;
    font-size: 1.2rem;
    font-weight: bold;
    color: #fff;

    @include mobile {
      @include roboto;
      font-size: 1.2rem;
      font-weight: bold;
    }
  }

  .tns-controls {
    button {
      width: 2.6rem;
      height: 2.6rem;
      background: url(../svg_icons/arrow-short-right.svg) no-repeat center;
      background-size: 100% auto;
      font-size: 0;
      position: absolute;
      right: 1.7rem;
      top: 50%;
      transform: translateY(-50%);
      z-index: 50;
      transition: transform .3s;

      @include hover {
        transform: translate(.2rem, -50%);
      }

      &:first-child {
        display: none;
      }
    }
  }
}
