.counter {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background: #fff;
  border-radius: 1px;
  height: 3.3rem;
  $self: &;

  &--default {
    height: 4rem;
  }

  &__main {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    border: 1px solid #000;
    padding: 0 .6rem;
    height: 100%;
    order: 0;
    transition: border-color .3s;

    #{$self}__cart:hover + & {
      border-color: red;
    }
  }

  &__btn {
    line-height: 0;
    position: relative;
    width: .9rem;
    height: 100%;
    display: flex;
    align-items: center;

    &--prev {
      margin-right: .2rem;
    }

    &--next {
      margin-left: .2rem;
    }
  }

  &__icon {
    width: .9rem;
    height: .9rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: opacity .3s;

    &--default {
      opacity: 1;

      #{$self}__btn:hover & {
        @include fromDesktop {
          opacity: 0;
        }
      }
    }

    &--hover {
      opacity: 0;

      #{$self}__btn:hover & {
        @include fromDesktop {
          opacity: 1;
        }
      }
    }
  }

  &__number-wrap {
    flex: 1;
  }

  &__number {
    text-align: center;
    @include roboto;
    font-size: 1.4rem;
    width: 27px;

    #{$self}--default & {
      margin: 0 1.6rem;
    }
  }

  &__cart {
    background: #000;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 0 .9rem 0 .7rem;
    height: 100%;
    transition: background .3s;
    order: 1;

    &:hover {
      @include fromDesktop {
        background: $red;
      }
    }
  }

  &__cart-img {
  }

  &__cart-text {
    font-size: 1.2rem;
    font-weight: 500;
    color: #fff;
    margin-left: .7rem;
    @include mobile {
      display: none;
    }
  }
}
