.check-list {
  &__list {
    margin: -.1rem;

    @include mobile {
      margin: -.1rem;
    }
  }

  &__li {
    padding: .1rem;

    @include mobile {
      padding: .1rem;
    }
  }

  &__item {
    background: #F5F5F5;
    position: relative;
    font-size: 1.3rem;
    line-height: 2.2rem;
    padding-left: 6rem;
    padding-top: 1rem;
    padding-right: 1rem;
    padding-bottom: 1.2rem;

    @include mobile {
      padding-top: 1.6rem;
      padding-bottom: 2.5rem;
      padding-left: 5.3rem;
      font-size: 1.1rem;
      line-height: 1.3rem;
    }

    &:before {
      display: block;
      content: '';
      width: 1.7rem;
      height: 1.7rem;
      background: url(../svg_icons/check.svg) no-repeat center;
      background-size: 100% auto;
      position: absolute;
      left: 1.8rem;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
