.product {
  color: $black;

  &__container {
    display: flex;
    flex-wrap: wrap;
  }

  &__content-col {
    flex: 1;
  }

  &__aside-col {
    width: 33.9rem;
    margin-left: 4.2rem;
    margin-top: .3rem;

    @include mobile {
      width: 100%;
      margin-left: 0;
      margin-top: 1.7rem;
      margin-bottom: 1.8rem;
    }
  }

  &__content-container {
    display: flex;
    flex-wrap: wrap;
    border-top: 4px solid #E5E5E5;
    padding-top: 1.7rem;

    @include mobile {
      padding-top: 0;
      border-top: 0;
    }
  }

  &__content-left-col {
    width: 63rem;

    @include desktop {
      width: 100%;
    }
  }

  &__gallery {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 1.9rem;

    @include mobile {
      display: block;
    }
  }

  &__small-gallery {
    width: 8rem;
    padding-top: .5rem;

    @include mobile {
      display: none;
    }
  }

  &__small-gallery-list {
    margin: -.4rem;
  }

  &__small-gallery-li {
    padding: .4rem;
  }

  &__small-gallery-item {
    display: block;
  }

  &__big-gallery {
    flex: 1;
    padding-left: 1rem;
    display: block;
    text-align: left;

    @include mobile {
      padding-left: 0;
      flex: none;
      text-align: center;
      position: relative;
      padding-bottom: 2.3rem;
    }
  }

  &__suppliers-desktop {
    @include desktop {
      display: none;
    }
  }

  &__suppliers-normal-desktop {
    display: none;
    margin-top: 3.2rem;

    @include desktop {
      display: block;
    }
  }

  &__content {
    flex: 1;
    padding-left: 4.2rem;

    @include desktop {
      flex: none;
      width: 100%;
      max-width: 50rem;
      padding-left: 0;
    }
  }

  &__header {
    font-size: 2.4rem;
    font-weight: bold;

    @include desktop {
      width: 100%;
    }

    @include mobile {
      font-size: 1.8rem;
      line-height: 2.2rem;
      margin-bottom: .8rem;
    }
  }

  &__header-desc {
    font-size: 1.6rem;
    margin-bottom: 2rem;
    margin-bottom: 1.4rem;

    @include desktop {
      width: 100%;
    }

    @include mobile {
      font-size: 1.2rem;
    }
  }

  &__title {
    font-size: 1.6rem;
    font-weight: bold;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 1.8rem;

    @include mobile {
      justify-content: space-between;
      margin-bottom: 1rem;
    }
  }

  &__title-text {
    margin-right: 1.8rem;

    @include mobile {
      margin-right: 0;
      font-size: 1.4rem;
    }
  }

  &__list {
    margin: -.9rem;

    @include mobile {
      margin: -0.7rem;
    }
  }

  &__li {
    padding: .9rem;

    @include mobile {
      padding: 0.7rem;
    }
  }

  &__list-title {
    font-size: 1.4rem;
    font-weight: 500;
    color: #000;
    margin-bottom: 1rem;

    @include mobile {
      font-size: 1.2rem;
      margin-bottom: .6rem;
    }
  }

  &__title-key {
    color: #7F7F7F;
  }

  &__title-value {
    margin-left: 4px;
  }

  &__tag-list {
    display: flex;
    flex-wrap: wrap;
    margin: -.5rem;

    @include mobile {
      display: block;
      overflow: auto;
      white-space: nowrap;
      font-size: 0;
      max-width: calc(100vw - 3rem);
    }
  }

  &__tag-li {
    padding: .5rem;

    @include mobile {
      display: inline-block;
    }
  }

  &__spec-header {
    margin-top: 2.6rem;
    font-size: 1.6rem;
    font-weight: bold;
    margin-bottom: 1.7rem;

    @include mobile {
      font-size: 1.4rem;
      margin-top: 0;
      margin-bottom: 1.2rem;
    }
  }

  &__spec-list {
    margin: -.9rem;

    @include mobile {
      margin: -1.3rem;
    }
  }

  &__spec-li {
    padding: .9rem;

    @include mobile {
      padding: 1.3rem;
    }
  }

  &__spec-title {
    font-size: 1.4rem;
    font-weight: 500;
    color: #696969;
    margin-bottom: .8rem;

    @include mobile {
      font-size: 1.2rem;
    }
  }

  &__specs-list {
    margin: -.4rem
  }

  &__specs-li {
    padding: .4rem;
  }

  &__specs-item {
    padding-bottom: .6rem;
    border-bottom: 1px solid #C7C7C7;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    font-size: 1.3rem;

    @include mobile {
      font-size: 1.1rem;
    }
  }

  &__specs-key {
    color: #7F7F7F;
  }

  &__specs-value {
  }

  &__desktop {
    @include desktop {
      display: none;
    }
  }

  &__normal-desktop {
    display: none;

    @include desktop {
      display: block;
    }
  }

  &__mobile-hide{
    @include mobile {
      display: none;
    }
  }

  &__mobile {
    display: none;

    @include mobile {
      display: block;
    }
  }

  &__check-list {
  }

  &__suppliers {
    @include mobile {
      margin-top: 1.8rem;
    }
  }
}
