.tag {
  $self: &;
  display: inline-block;

  &__input {
    display: none;
  }

  &__text {
    display: inline-block;
    border: 1px solid #cfcfcf;
    border-radius: 3px;
    font-size: 1.4rem;
    color: #000;
    padding: .35rem 1.7rem;
    transition: background .3s, border-color .3s;

    @include mobile {
      font-size: 1.2rem;
      padding: .5rem 1.9rem;
    }

    #{$self}__input:checked + & {
      border-color: #EE7775;
      background: #FFECE7;
    }
  }
}
