.sort {
  $self: &;
  position: relative;

  &__header {
    width: 19rem;
    height: 3.2rem;

    @include mobile {
      width: 100%;
    }

    #{$self}.active & {
      .icon {
        transform: translate(0, -50%) rotate(-180deg);
      }
    }
  }

  &__content {
    z-index: 500;
    position: absolute;
    left: 0;
    top: calc(100%);
    right: 0;
    padding: 1.3rem 1.6rem 2rem;
    background: #000;
    opacity: 0;
    pointer-events: none;
    transition: opacity .3s;

    #{$self}.active & {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &__list {
    margin: -.6rem;
  }

  &__li {
    padding: .6rem;
  }

  &__link {
    font-size: 1.3rem;
    font-weight: bold;
    color: #ffffff;
    display: block;
    transition: color .3s;

    @include hover {
      color: $red;
    }
  }
}
