.pagination {
  $self: &;
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  @include mobile {
    display: none;
  }

  &__btn {
    line-height: 0;

    &--prev {
      margin-right: 1.7rem;
    }

    &--next {
      margin-left: 1.7rem;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: -.05rem;
  }

  &__li {
    padding: .05rem;
  }

  &__item {
    width: 5rem;
    height: 4.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $grey;
    @include circe;
    font-size: 1.4rem;
    font-weight: bold;
    color: #4A4A4A;
    transition: background .3s, color .3s, border-color .3s;

    #{$self}__li.active & {
      background: #F6F7F8;

      @include hover {
        background: $red;
      }
    }

    @include hover {
      background: $red;
      color: #fff;
    }
  }
}
