.product-cart {
    $self: &;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    &--cart {
        padding: 0 1.8rem 0 5.5rem;
    }
    &__close {
        position: absolute;
        font-size: 1rem;
        background: #dcdcdc;
        color: #fff;
        line-height: 0;
        border-radius: 100%;
        width: 22px;
        height: 22px;
        left: -3.4rem;
        top: calc(50% + .9rem);
        transform: translateY(-50%);
        transition: background .3s;

        #{$self}--cart & {
            left: -5.4rem
        }
    }

    &__img {
        &-container {
            position: relative;
            width: 11rem;
            max-height: 11rem;
        }

        &-box {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
            transform: translateX(-0.4rem);
        }
    }

    &__content {
        flex: 1;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-left: 1.4rem;
        padding-right: 2.8rem;

        #{$self}--cart & {
            padding-left: 2.5rem;
        }
    }

    &__title {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: .7rem;

        #{$self}--cart & {
            max-width: 260px;
            margin-bottom: 0;
            margin-top: 0.9rem;
        }

        &-text {
            font-size: 1.3rem;
            font-weight: 400;
            line-height: 1.7rem;
            color: #000;
            transition: color .3s;

            #{$self}--cart & {
                font-size: 1.4rem;
                line-height: 1.9rem;
            }
        }
    }

    &__desc {
        font-size: 1.1rem;
        line-height: .9rem;
        color: #9299a2;

        #{$self}--cart & {
            margin-bottom: 1rem;
        }
    }

    &__price {
        font-size: 1.5rem;
        line-height: 3rem;
        color: #4a4a4a;
        margin-left: 1.9rem;

        #{$self}--cart & {
          margin-top: .6rem;
          margin-left: 4rem;
          font-size: 1.8rem;
          font-weight: 500;
        }
    }

    &__counter {
        align-self: flex-start;
        margin-left: auto;
        margin-top: 0.5rem;

        .counter__btn--prev {
            margin-right: 0.7rem;
        }

        .counter__btn--next {
            margin-left: 0.7rem;
        }
    }
}
