.product-category {
  $self: &;
  border: 4px solid #000;
  padding: 1.7rem 2.2rem;
  margin-bottom: 1.5rem;
  overflow: hidden;

  &--mobile {
    display: none;

    @include mobile {
      display: block;
    }
  }

  &__list {
    display: none;

    &.active {
      display: block;
      @include fromDesktop {
        animation: slideToRight .5s both;
      }
    }
  }

  &__li {
    &.hide {
      display: none;
    }
  }

  &__btn {
    font-size: 1.3rem;
    font-weight: bold;
    color: #000;
    position: relative;
    padding-right: 1.5rem;

    &--root {
      #{$self}.active & {
        margin-bottom: 1.2rem;
        padding-right: 0;

        .product-category__btn-img {
          right: calc(100% + .5rem);
          transform: rotate(180deg);
          top: calc(50% - .4rem);
        }
      }
    }

    &.hide {
      display: none;
    }
  }

  &__btn-img {
    position: absolute;
    right: 0;
    top: calc(50% + .1rem);
    transform: translateY(-50%);
  }
}
