.btn {
  $self: &;
  width: 100%;
  height: 4.8rem;
  border: 1px solid rgba(#000, .85);
  padding: 0 5rem 0 1.6rem;
  position: relative;
  display: flex;
  align-items: center;

  @include mobile {
    padding-right: 6rem;
    padding-left: 1.3rem;
  }

  &--no-img {
    padding: 0 1.6rem;
  }

  &--arrow-down {
    @include fromDesktop {
      &:hover {
        .btn__icon {
          transform: translate(0, -35%)!important;
        }
      }
    }
  }

  &--center {
    justify-content: center;
    padding: 0 1.6rem;
    height: 4rem;
  }

  &--red {
    background: $red;
    color: #fff;
    border-color: $red;
  }

  &--black {
    background: $black;
  }

  &__text {
    @include muller;
    font-size: 1.6rem;
    font-weight: 700;
    color: #000;

    @include mobile {
      transform: translateY(.2rem);
    }

    #{$self}--red & {
      color: #fff;
      font-weight: bold;
      letter-spacing: 0.13rem;
    }

    #{$self}--select & {
      font-size: 1.3rem;
    }

    #{$self}--center & {
      font-size: 1.4rem;
      font-weight: 500;
      letter-spacing: normal;
    }

    #{$self}--black & {
      color: #fff;
    }
  }

  &__icon {
    position: absolute;
    right: 2rem;
    top: 50%;
    transform: translateY(-50%);
    font-size: 2.6rem;
    transition: transform .3s;

    @include fromDesktop {
      #{$self}:hover & {
        transform: translate(.5rem, -50%);
      }
    }

    @include fromDesktop {
      transform: translate(0, -50%)!important;
    }

    @include mobile {
      right: 3rem;
    }

    #{$self}--red & {
      color: #fff;
    }

    #{$self}--arrow-down & {
      font-size: 1.6rem;
      right: 1.6rem;
    }

    #{$self}--select & {
      font-size: 1.3rem;
      right: 1.2rem;
    }

    #{$self}--center & {
      position: absolute;
      left: 1.2rem;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
