.card {
  $self: &;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  &__content {
    padding: 0 .7rem .8rem .7rem;
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    @include fromDesktop {
      padding: 0 1rem .8rem 1rem;
    }
  }

  &__img-box {
    height: 21.6rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 1.5rem;

    @include mobile {
      height: 11.1rem;
    }
  }

  &__img {
    max-width: 90%;
    max-height: 90%;

    @include mobile {
      max-width: 80%;
      max-height: 80%;
    }
  }

  &__quantity {
    width: 100%;
    font-size: 1.2rem;
    color: #4a4a4a;
    margin-bottom: .3rem;
    padding-left: 1rem;
    margin-bottom: -1.5rem;

    @include mobile {
      font-size: .8rem;
    }
  }

  &__title {
    font-size: 1.3rem;
    font-weight: 400;
    line-height: 2rem;
    margin-bottom: .2rem;
    color: #000;

    @include mobile {
      font-size: 1rem;
      line-height: 1.2rem;
    }
  }

  &__price {
    font-size: 1.7rem;
    font-weight: 500;
    color: #E02020;
    margin-bottom: .9rem;

    @include mobile {
      font-size: 1.2rem;
      margin-bottom: .4rem;
    }
  }

  &__desc {
    font-size: 1.1rem;
    color: #9299A2;
    margin-top: .3rem;

    @include mobile {
      font-size: 1rem;
    };
  }

  &__bottom {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    transition: background .3s;
    padding: .8rem .7rem;
    margin-top: 1.1rem;

    @include fromDesktop {
      padding: .8rem 1.4rem 1.4rem;
    }

    &:before {
      display: block;
      content: '';
      position: absolute;
      left: .8rem;
      right: .8rem;
      top: 0;
      height: 1px;
      background: #e5e5e5;
    }

    &.active {
      background: $red;

      &:before {
        display: none;
      }

      .counter {
        display: flex;
      }
    }
  }

  &__cart {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__cart-btn {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-right: 1rem;

    #{$self}__bottom.active & {
      display: none;
    }
  }

  &__cart-icon {
    margin-right: 1.1rem;

    @include mobile {
      width: 1.2rem;
      height: 1.2rem;
      margin-right: .5rem;
    }
  }

  &__cart-text {
    font-size: 1.4rem;
    font-weight: 500;
    color: #686868;

    @include mobile {
      font-size: 1rem;
    }
  }

  &__layers {
    width: 3rem;
    height: 3rem;
    color: #000;
    font-size: 1.8rem;
    line-height: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #000;
    transition: background .3s, color .3s;

    @include hover {
      background: #000;
      color: #fff;
    }

    @include mobile {
      font-size: 1.2rem;
    }

    #{$self}__bottom.active & {
      color: #fff;
    }
  }

  &__bottom-col {
    flex: 1;
    flex-direction: column;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  .counter {
    height: 3rem;
  }
}
