.products {
  color: #000;

  &__head {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.2rem;

    @include mobile {
      display: block;
      margin-bottom: 2.4rem;
    }
  }

  &__header {
    font-size: 2.6rem;
    text-transform: uppercase;
    font-weight: 700;

    @include mobile {
      font-size: 2.6rem;
      line-height: 4.8rem;
      margin-bottom: .6rem;
    }
  }

  &__btn {
    width: 19rem;
    height: 3.1rem;

    @include mobile {
      width: 100%;
    }
  }

  &__content-col {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;

    @include mobile {
      display: block;
    }
  }

  &__aside-col {
    width: 23rem;
    position: relative;

    @include mobile {
      display: none;
    }
  }

  &__aside-title > a {
    font-size: 1.3rem;
    font-weight: bold;
    margin-bottom: 1rem;
    color: #000;
  }

  &__content {
    flex: 1;
    padding-left: 2rem;

    @include mobile {
      flex: none;
      padding-left: 0;
    }
  }

  &__aside-list {
    position: absolute;
    height: 0;
    background: #ffffff;
    left: 100%;
    top: 0;
    width: 100%;
    opacity: 0;
    transition: .4s ease;
    z-index: -99;
    margin-top: 0.5rem;
  }


  &__aside-col-group{
    transition: .4s ease;
    &.active {
      transform: translateX(-100%);
    }
  }
  &__aside-group {
    &.active {
      .products__aside-list {
        // visibility: visible;
        opacity: 1;
        z-index: 2;
        height: 100%;
      }
    }
  }
  &__aside-back-btn {
    display: block;
    font-size: 1.3rem;
    font-weight: bold;
    color: #000;
    cursor: pointer;
    margin-bottom: 1rem;
    &:before {
      content: '◀';
      position: absolute;
      font-size: 8px;
      left: -12px;
      top: 3px;
    }
  }

  &__aside-li {
    padding: .3rem;
  }

  &__aside-link {
    font-size: 1.3rem;
    color: #000;
  }

  &__show-more {
    display: none;

    @include mobile {
      display: flex;
      margin-top: 2rem;
    }
  }
  &__filter_group {
    border-bottom: 1px solid rgba($color: #000000, $alpha: .1);
    padding-bottom: .5rem;
    margin-bottom: 0.5rem;
    &:last-child {
      border-bottom: none;
    }
  }
  &__filter_title {
    font-size: 1.3rem;
    font-weight: 500;
    color: #000;
    margin: 1.5rem 0;
  }
  .products__filter_checkbox label {
    display: block;
    position: relative;
    padding-left: 2rem;
    cursor: pointer;
    font-size: 1.3rem;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  .products__filter_checkbox {
    margin-bottom: .5rem;
  }

  .products__filter_checkbox label input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  .checkmark {
    position: absolute;
    top: 2px;
    left: 0;
    height: 1.4rem;
    width: 1.4rem;
    background-color: rgba(0,0,0,.1);
    border: 1px solid rgba(0,0,0,.1);
    border-radius: 3px;
  }

  .products__filter_checkbox label :hover input ~ .checkmark {
    background-color: rgba(0,0,0,.1);;
  }

  .products__filter_checkbox label input:checked ~ .checkmark {
    background-color: #E02020;
  }

  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the checkmark when checked */
  .products__filter_checkbox label input:checked ~ .checkmark:after {
    display: block;
  }

  /* Style the checkmark/indicator */
  .products__filter_checkbox label .checkmark:after {
    left: 4px;
    top: 1px;
    width: 4px;
    height: 8px;
    border: solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  .js-catalog-sorting {
    position: relative;
    &__list {
      position: absolute;
      width: 100%;
      background: #000;
      padding: 20px 16px;
      height: 0;
      opacity: 0;
      transition: height .2s ease, opacity .2s ease;
      > a {
        display: block;
        color: #ffffff;
        font-family: 'roboto Neue';
        font-weight: 700;
        margin-bottom: 27px;
        opacity: 0;
        &:hover {
          color: #E02020;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &.active {
      .js-catalog-sorting__list {
        height: 166px;
        opacity: 1;
        > a {
          opacity: 1;
          transition: opacity .2s ease .2s;
        }
      }
      .icon {
        transform: translateY(-.8rem) rotate(180deg) !important;
      }
    }
  }

}
