.select {
  display: inline-block;
  position: relative;
  z-index: 500;
  $self: &;

  &--fullwidth {
    display: block;
  }

  &.active {
    z-index: 600;
  }

  @include mobile {
    width: 100%;
  }

  &__btn {
    display: flex;
    align-items: center;
    font-size: 1.4rem;
    font-weight: bold;
    color: #fff;
    height: 3rem;
    min-width: 11.9rem;
    background: #212121;
    padding-left: 2rem;
    padding-right: 4rem;

    @include mobile {
      width: 100%;
    }

    #{$self}--field & {
      background: none;
      height: 4.2rem;
      border: 1px solid rgba(#000, .25);
      font-size: 1.6rem;
      font-weight: 400;
      color: #000;
      padding-left: 1.7rem;
      padding-right: 4.5rem;
    }

    #{$self}--country & {
      padding-left: .8rem;
      min-width: 13.6rem;
    }

    #{$self}--fullwidth & {
      width: 100%;
    }
  }

  &__country {
    margin-right: .9rem;
  }

  &__text {
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    flex: 1;
    text-align: left;
  }

  &__icon {
    position: absolute;
    right: 1.6rem;
    top: 50%;
    transform: translateY(-50%);
    font-size: 1.4rem;
    transition: transform .3s;

    #{$self}--field & {
      color: rgba(#000, .5);
    }

    #{$self}.active & {
      transform: translateY(-50%) rotate(180deg);
    }
  }

  &__list {
    display: none;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    left: 0;
    top: calc(100% + 0px);
    right: 0;
    max-height: 30rem;
    overflow: auto;
    overflow-x: hidden;
    background: #212121;
    z-index: 999;

    #{$self}--field & {
      background: #fff;
      border: 1px solid rgba(#000, .25);
      border-top: 0px;
    }

    #{$self}.active & {
      display: block;
      opacity: 1;
      pointer-events: auto;
    }
  }

  &__li {
  }

  &__item {
    color: #fff;
    font-size: 1.4rem;
    font-weight: 700;
    cursor: pointer;
    padding: 1.2rem 1.2rem;
    @include roboto;
    transition: background .3s, color .3s;

    #{$self}--field & {
      color: rgba(#000, .5);
    }

    @include hover {
      background: $red;
      color: #fff;
    }
  }
}
