.join {
  margin-bottom: 7.1rem;

  @include mobile {
    margin-bottom: 4.9rem;
  }

  &__container {
    display: flex;
    flex-wrap: wrap;

    @include mobile {
      flex-direction: column;
    }
  }

  &__aside {
    width: 20rem;
    text-align: center;
    display: flex;
    flex-direction: column;

    @include mobile {
      order: 1;
      width: 100%;
      display: block;
    }
  }

  &__content {
    width: calc(100% - 20rem);
    padding-left: 5rem;

    @include mobile {
      order: 0;
      padding-left: 0;
      flex: none;
      width: 100%;
      margin-bottom: 2.4rem;
    }
  }

  &__auth {
    margin: -.4rem;
  }

  &__auth-li {
    padding: .4rem;
  }

  &__reset-pass {
    font-size: 1.2rem;
    color: rgba(#000, .7);
    display: inline-block;
    margin-top: 1.7rem;
    transition: color .3s;

    @include mobile {
      letter-spacing: 0.04rem;
    }

    @include hover {
      color: #000;
    }
  }

  &__btn-block {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    @include mobile {
      margin-top: 4rem;
    }
  }

  &__header {
    @include roboto;
    font-size: 1.7rem;
    line-height: 2.4rem;
    font-weight: bold;
    text-align: left;
    margin-bottom: 1rem;
  }

  &__btn-list {
    margin: -.45rem;
  }

  &__btn-li {
    padding: .45rem;
  }
}
