.select-delivery{
  width: 31.3rem;
  padding: 1rem 1.8rem 1.9rem 1.3rem;
  border: 0.4rem solid black;
  font-size: 1.4rem;
  &__title {
    padding-left: 0.3rem;
    margin: 1rem 0 0.4rem;
    font-weight: 700;
  }
  &__list {
    padding-left: 0.3rem;
  }
  &__item {
    margin-bottom: 1.5rem;
    line-height: 100%;
    &:nth-last-child(1) {
      margin-bottom: 0;
    }
  }
}