.filter {
  $self: &;
  position: relative;

  &__header {
    font-size: 1.3rem;
    font-weight: bold;
    color: #000;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }

  &__btn {
    font-weight: 400;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    font-size: 1.3rem;
    transition: color .3s;

    @include hover {
      color: $red;
    }

    &--close {
      display: none;
      position: absolute;
      right: 0;
      bottom: 0;

      #{$self}.active & {
        display: block;
      }
    }
  }


  &__btn-icon {
    margin-left: .8rem;
    #{$self}.active & {
      transform: rotate(45deg);
    }
  }

  &__content {
    display: none;

    #{$self}.active & {
      display: block;
    }
  }

  &__btn-text {
    &--expand {
      #{$self}.active & {
        display: none;
      }
    }

    &--minimize {
      display: none;

      #{$self}.active & {
        display: inline;
      }
    }
  }
}
