.search {
  display: flex;
  flex-wrap: wrap;

  @include mobile {
    display: block;
  }

  &__field-box {
    flex: 1;
    position: relative;
  }

  &__field {
    width: 100%;
    height: 4.2rem;
    border: 1px solid rgba(#000, .25);
    border-width: 1px 1px 1px 0;
    padding-left: 1.8rem;
    padding-right: 5rem;
    font-size: 1.6rem;
    color: #000;

    @include mobile {
      height: 3.8rem;
      padding-left: .9rem;
      padding-right: 4rem;
      border-width: 1px;
      font-size: 1.4rem;
      color: rgba(#000, .5);
    }

    &::placeholder {
      color: #000;
    }

    &::-webkit-input-placeholder {
      color: #000;
    }
  }

  &__submit {
    position: absolute;
    right: 1.4rem;
    top: 50%;
    transform: translateY(-50%);
    z-index: 5;

    @include mobile {
      width: 2rem;
      height: 2rem;
      line-height: 0;
      right: 1rem;
    }
  }

  &__submit-icon {
    @include mobile {
      width: 2rem;
      height: 2rem;
    }

  }

  &__select {
    @include mobile {
      display: none!important;
    }
  }

  &__category-select {
    @include mobile {
      display: none;
      width: 100%;
      height: 3.8rem;
      margin-bottom: 1.2rem;
    }
  }
}
