.card-cart {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  color: $black;

  @include mobile {
    position: relative;
    padding-top: 2.3rem;
  }

  &__close-col {
    width: 2.2rem;
    display: flex;
    align-items: center;

    @include mobile {
      width: 0;
    }
  }

  &__close {
    width: 2.2rem;
    height: 2.2rem;
    background: #d8d8d8;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    color: #fff;
    transform: translateY(.4rem);
    transition: background .3s;

    @include mobile {
      position: absolute;
      right: 0;
      top: 0;
      width: auto;
      height: auto;
      background: none;
      font-size: 1.1rem;
      color: #9299A2;
    }

    @include hover {
      background: $red;
    }
  }

  &__close-icon {
    @include mobile {
      display: none!important;
    }
  }

  &__text {
    display: none;

    @include mobile {
      display: block;
    }
  }

  &__img-col {
    display: block;
    width: 11rem;
    margin-left: 2.7rem;

    @include desktop {
      margin-left: .7rem;
    }

    @include mobile {
      margin-left: 0;
    }
  }

  &__img {
    max-width: 100%;
    height: auto;
  }

  &__name-col {
    width: 25.7rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 3rem;
    padding: .7rem 0 .7rem;

    @include desktop {
      width: 18.1rem;
      padding-top: .9rem;
      margin-left: 1rem;
    }

    @include mobile {
      width: calc(100% - 11rem);
      margin-left: 0;
      padding-left: 1rem;
      padding-top: 1.1rem;
      display: block;
    }
  }

  &__title {
    display: block;
    font-size: 1.4rem;
    line-height: 1.9rem;
    color: $black;
    transition: color .3s;

    @include hover {
      color: $red;
    }

    @include mobile {
      font-size: 1.2rem;
      line-height: 1.7rem;
      margin-bottom: 1.5rem;
    }
  }

  &__desc {
    font-size: 1.1rem;
    color: #9299A2;
  }

  &__counter-col {
    margin-left: auto;
    padding-top: .6rem;

    @include mobile {
      padding-top: 0;
      margin-top: 1.2rem;
      margin-left: 0;
    }
  }

  &__price-col {
    margin-left: 4rem;
    padding-top: .5rem;

    @include desktop {
      margin-left: 3rem;
    }

    @include mobile {
      margin-left: auto;
      padding-top: 1.4rem;
    }
  }

  &__price {
    font-size: 1.8rem;
    font-weight: 500;
    color: #4a4a4a;
  }

  &__counter {
    .counter__cart-text {
      display: block;
    }
  }
}
