.order-summ {
  $self: &;
  display: flex;
  justify-content: space-between;
  padding: 3.4rem 26rem 3.9rem 4.4rem;
  &__title {
    font-size: 2.7rem;
    line-height: 110%;
    font-weight: 700;
  }
  &__list {
    margin-left: auto;
  }
  &__item {
    margin-bottom: 1.1rem;
    &::after {
      content: '';
      display: block;
      width: 27.5rem;
      height: 0.1rem;
      margin-top: 0.8rem;
      margin-left: auto;
      background: rgba(0, 0, 0, 0.1);
    }
    &:last-child{
      margin-bottom: 0;
      &::after {
        content: none;
      }
    }
    &:nth-last-child(2) {
      margin-bottom: 0.5rem;
    }
  }
  &__text {
    font-size: 1.7rem;
    font-weight: 500;
    text-align: right;
    color: rgba(0, 0, 0, 0.42);
    #{$self}__item:last-child & {
      color: rgba(0, 0, 0, 1);
    }
  }
  &__name {
    display: inline-block;
    width: 18rem;
  }
  &__cost {
    display: inline-block;
    width: 12.6rem;
  }
}