.popup {
  $self: &;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba($black, .6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
  color: $black;

  &__content {
    background: #fff;
    max-height: 94vh;
    overflow-x: hidden;
    overflow-y: auto;

    #{$self}--default & {
      width: 87rem;
      padding: 6rem;
    }

    #{$self}--state & {
      width: 65rem;
      min-height: 38rem;
      display: flex;
      flex-wrap: wrap;
    }
  }

  &__header {
    font-size: 2.6rem;
    font-weight: bold;
    margin-bottom: 4.8rem;
  }

  &__btn-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin: -.7rem;
  }

  &__btn-li {
    padding: .7rem;
  }

  &__btn-create {
    width: 22rem;

    .btn__text {
      font-weight: 500;
    }
  }

  &__btn-cancel {
    width: 11.5rem;
    justify-content: center;

    .btn__text {
      font-weight: 500;
    }
  }

  &__left-col {
    width: 22.7rem;
    background: #F2F2F2;
    padding-top: 3rem;
    display: flex;
    justify-content: center;
    align-items: flex-start;
  }

  &__right-col {
    flex: 1;
    padding: 7rem 3rem 3rem 5rem;
    display: flex;
    flex-direction: column;
  }

  &__state-header {
    font-size: 3rem;
    line-height: 3.7rem;
    font-weight: bold;
    margin-bottom: 2rem;
  }

  &__state-text {
    font-size: 1.4rem;
    line-height: 2.2rem;
    max-width: 32rem;
  }

  &__btn-state {
    width: 11.5rem;
    margin-top: auto;
    margin-left: auto;
  }
}
