.catalog-slider {
  color: #fff;
  position: relative;
  padding-bottom: 3rem;

  &__item {
    padding: 3rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    background: no-repeat center;
    background-size: cover;

    @include mobile {
      padding: 4rem 2.5rem;
    }
  }

  &__header {
    @include muller;
    font-size: 4.1rem;
    font-weight: bold;
    line-height: 3.9rem;
    width: 24rem;
    text-transform: uppercase;

    @include mobile {
      width: 100%;
      font-size: 2rem;
      line-height: normal;
      margin-bottom: 2rem;
    }
  }

  &__icon {
    margin-left: 1.8rem;

    @include mobile {
      display: none;
    }
  }

  &__desc {
    font-size: 2.1rem;
    line-height: 2.1rem;
    width: 32.1rem;
    margin-left: 2rem;

    @include mobile {
      font-size: 1.5rem;
      line-height: normal;
      width: 100%;
      margin-left: 0;
    }
  }
}
