.card-info {
  border: 4px solid $black;
  padding: 1.0rem 1.7rem;
  color: $black;

  &__btn {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    font-size: 1.4rem;
    font-weight: bold;
    padding-bottom: 1.2rem;
    border-bottom: 1px solid $black;
    padding-right: .9rem;
    margin-bottom: 1.1rem;
  }

  &__list {
    margin: -.4rem;
  }

  &__li {
    padding: .4rem;
  }

  &__item {
    font-size: 1.4rem;
  }
}
