.category-select {
  $self: &;
  width: auto;
  border: 1px solid rgba(#000, .25);
  position: relative;
  color: #000;

  &__header {
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 0 1.8rem;
    position: relative;
    z-index: 20;
    background: #fff;
  }

  &__header-text {
    font-size: 1.6rem;
    color: rgba(#000, .85);
    margin-right: .5rem;
  }

  &__header-icon {
    font-size: 1.5rem;
    line-height: 0;
    color: rgba(#000, .5);
    transition: transform .3s;

    #{$self}.active & {
      transform: rotate(-180deg);
    }
  }

  &__list {
    display: none;
    position: absolute;
    left: 0;
    top: calc(100% + .1rem);
    background: #fff;
    z-index: 10;
    box-shadow: 0 0 10px 1px rgba(#000, .25);
    padding: .7rem 0;
    z-index: 99;

    &.active {
      display: block;
    }

    .category-select__list {
      top: -.7rem;
      left: calc(100%);
    }
  }

  &__li {
    position: relative;
    &:last-child {
      button > svg {
        display: none
      }

    }

    .category-select {
      &__list {

      }
    }
  }

  &__item {
    position: relative;
    z-index: 30;
    background: #fff;
  }

  &__list-header {
    font-size: 1.3rem;
    white-space: nowrap;
    position: relative;
    padding: .7rem 2.8rem .7rem 1.8rem;
    width: 100%;
    text-align: left;
    transition: color .3s;
    color: #000;
    display: block;

    &.active {
      color: $red;
    }

    @include hover {
      color: $red;
    }
  }

  &__list-header-icon {
    font-size: 1rem;
    color: #000;
    margin-left: 1rem;
    margin-right: 1.6rem;
    position: absolute;
    right: -8px;
    top: 9px;
    transition: color .3s;

    #{$self}__list-header.active & {
      color: $red;
    }
  }

  &__sub-list {
    display: none;
  }

  &__link {
    display: block;
    font-size: 1.3rem;
    color: #000;
    transition: color .3s;
    white-space: nowrap;
    width: 100%;
    padding: .7rem 1.8rem;

    &:hover {
      color: $red;
    }
  }
}
