.cart-list {
  $self: &;
  border: 3px solid #e5e5e5;
  padding: .6rem 3.7rem 1.8rem 2.7rem;
  color: $black;

  @include desktop {
    padding: .6rem 1.8rem 1.8rem 1.8rem;
  }

  @include mobile {
    border: none;
    padding: 0;
  }

  &__header {
    font-size: 1.4rem;
    padding-bottom: .6rem;
    border-bottom: 1px solid #d8d8d8;
    margin-bottom: 1rem;

    @include mobile {
      font-size: 1.2rem;
      margin-bottom: .3rem;
      margin-left: -1.5rem;
      width: calc(100% + 3rem);
      padding-left: 1.5rem;
      padding-right: 1.5rem;
    }
  }

  &__header-key {
    margin-right: .2rem;
  }

  &__header-value {
    font-weight: bold;
  }

  &__list {
    margin: -.6rem;
  }

  &__li {
    position: relative;
    padding: .6rem;

    &:after {
      display: block;
      content: '';
      position: absolute;
      top: 100%;
      left: .7rem;
      right: .7rem;
      transform: translateY(-50%);
      height: 1px;
      background: #d8d8d8;
    }

    &:last-child:after {
      display: none;
    }
  }

  &__card-list {
    margin: -1rem;

    @include mobile {
      margin: -1.2rem;
    }
  }

  &__card-li {
    position: relative;
    padding: 1rem;

    @include mobile {
      padding: 1.2rem;
    }

    &:after {
      display: block;
      content: '';
      position: absolute;
      top: 100%;
      left: 1rem;
      right: 1rem;
      transform: translateY(-50%);
      height: 1px;
      background: #d8d8d8;

      @include mobile {
        left: -1.5rem;
        right: -1.5rem;
      }
    }

    &:last-child:after {
      display: none;
    }
  }
}
