.navigation {
  $self: &;
  margin-bottom: 5.8rem;
  transition: background .3s;

  @include mobile {
    margin-bottom: 1.2rem;
  }

  &.active {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 999;
    box-shadow: 0 5px 10px -5px rgba(#000, .3);
    padding: 1rem 0;
    background: #fff;

    @include mobile {
      position: static;
      z-index: 1;
      box-shadow: none;
    }

    .cart-popup {
      top: calc(100% + 1.5rem);
    }
  }

  &--static {
    margin-bottom: 0;

    &.active {
      background: #000;
      margin: 0;
    }

    @include mobile {
      margin-top: 1.3rem;
      margin-bottom: -.5rem;
    }

    .search {
      &__field {
        border: none;
      }
    }

    .select {
      &__btn {
        border-width: 0 1px 0 0;
        border-color: #000;
      }
    }
  }

  &__logo {
    @include mobile {
      display: none;
    }
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    #{$self}--static & {
      padding: 0;
    }
  }

  &__search {
    margin-left: 4.3rem;
    flex: 1;

    @include mobile {
      margin-left: 0;
    }

    #{$self}--static & {
      background: #fff;

      @include mobile {
        background: none;
      }
    }
  }

  &__cart {
    display: flex;
    flex-wrap: wrap;
    margin-left: 2rem;
    position: relative;
    cursor: pointer;

    @include mobile {
      display: none;
    }
    &:hover {
      .navigation__cart-icon {
        color: #fff
      }
    }
  }

  &__cart-icon-wrap {
    position: relative;
    margin-right: 1.8rem;
  }

  &__cart-icon {
    color: $red;
    font-size: 3.6rem;
  }

  &__cart-text {
    width: 7rem;
    font-size: 1.4rem;
    line-height: 1.5rem;
    color: #000;
    font-weight: 500;
    @include roboto;
    margin-top: .5rem;

    #{$self}--static & {
      color: #fff;
    }
  }
}
