.breadcrumbs {
  margin-bottom: 10px;
  $self: &;

  &__container {
    @include mobile {
      padding: 0 1rem!important;
    }
  }

  &__list {
    font-size: 0;
    margin: -.7rem;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
  }

  &__li {
    position: relative;
    padding: .7rem;
    display: inline-block;

    &:after {
      display: block;
      content: '';
      width: .3rem;
      height: .4rem;
      position: absolute;
      left: 100%;
      background: #ccc;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    &:last-child:after {
      display: none;
    }
  }

  &__link {
    display: block;
    font-size: 1.2rem;
    color: #696969;
  }
}
